body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

body{
  background-color: lightgray;
}

#page-container {
  position: relative;
  min-height: 100vh;
}
#content-wrap {
  padding-bottom: 3rem; /* Footer height */
}
#footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 3rem; /* Footer height */
}

.footer-content{
  padding-left: 15px;
  font-size: 13px;
  top: 0;
}

.footer-hr{
  margin-top: 0px;
  bottom: 0;
}

.apod-img {
  display: block;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.apod-header {
  text-align: center;
}

.apod-wrapper {
  font-size: 19px;
  font-family: 'Lora', serif;
}

.apod-date {
  display: block;
  margin-left: 5%;
  font-size: 15px;
}

.apod-content {
  display: block;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.apod-credits {
  text-align: center;
  font-size: 15px;
}
.apod-form {
  display: block;
  margin-left: 5%;
}

.search-btn {
  margin-left: 8px;
  padding: 2px;
}
#page-numbers {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  width: 90%;
}

#page-numbers > li {
  margin-right: 0.3em;
  color: blue;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  padding: 3px;
  border: 1px  solid #000;
}

.submit-btn {
  margin-top: 10px;
}

.gallery {
  margin: 5px;
  border: 1px solid rgb(155, 155, 155);
  float: left;
  width: 200px;
  height: 300px;
}

.gallery:hover {
  border: 1px solid #555;
}

.gallery img {
  width: 100%;
  height: auto;
}

.desc {
  padding: 5px;
  text-align: center;
  font-size: 13px;
}
.time-box{
  padding: 5px;
}

.time-box label{
  padding-right: 20px;
}

.rover-box{
  padding: 5px;
}

.rover-box label{
  padding-right: 20px;
}

.field-box{
  margin-top: 18px;
}

.form-box{
  margin-top: 30px;
}
.curiosity-select{
  width: 350px;
  margin: 5px 20px;
}

.spirit-select{
  width: 350px;
  margin: 5px 20px;
}
.display-time-box{
  margin: 5px 20px;
}
.rover-container{
  padding: 20px;
  font-family: 'Lora', sans-serif;
  font-size: 19px;
}

.rover-header {
  text-align: center;
}
.header{
  background-color: blueviolet;
  overflow: hidden;
}

.header a{
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 15px 17px;
  text-decoration: none;
  font-size: 17px;
  font-family: 'Roboto', sans-serif;
}

.header img{
  float: left;
  display: block;
  padding: 7px 16px;
  width: 35px;
  height: 35px;
}

.header a:hover {
  background-color: #ddd;
  color: #000;
}
.home-container{
  margin: 20px;
  font-size: 19px;
  font-family: 'Lora', serif;
}

.home-header{
  text-align: center;
}
